export function addTodo(text, group) {
	let item = { text, ...{ group } };
	return {
		type: "ADD_TODO",
		text,
		group,
	};
}

export function removeTodo(todo) {
	return {
		type: "REMOVE_TODO",
		todo,
	};
}
export function undoTodo(todo) {
	return {
		type: "UNDO_TODO",
		todo,
	};
}
export function reset() {
	return {
		type: "RESET_LOTTO",
	};
}
export function setLottoItems(data) {
	return {
		type: "SET_LOTTO_ITEMS",
		data,
	};
}
