import { h, Component, createRef } from 'preact';
import { connect } from 'preact-redux';
import reduce from '../reducers';
import * as actions from '../actions';
import _ from 'lodash';
import qs from 'qs';
import useSWR, { SWRConfig } from 'preact-swr';
import fetch from 'unfetch';
import Swal from 'sweetalert2';
import { authToken } from '../config';
import toastr from 'toastr';

// import { fetcher } from '../utils/fetcher';

@connect(reduce, actions)
export default class App extends Component {
  state = {
    reverse: false,
    text: '',
    previewText: '',
    group: '',
    groupSelected: [],
    reverse2: false,
    reverse3: false,
    autoPrice: 0,
    items: [],
  };

  timeOutPreview = null;

  constructor(props) {
    super(props);

    this.numberRef = new createRef();
    this.inputAutoPrice = new createRef();
    this.setState({
      reverse: false,
    });

    // fetch('http://192.168.11.2/api/smartcs/projects/all"')
    //   .then((r) => r.text())
    //   .then((txt) => console.log(txt));
  }

  shuffleNumber(number, digit) {
    let maxNumber = 3;
    console.log(number, digit, maxNumber);
    number = number.toString();
    if (Number(number) < 2) return;
    var items = [];
    items[1] = number.substr(0, 1);
    items[2] = number.substr(1, 1);
    items[3] = '';
    if (maxNumber > 2) items[3] = number.substr(2, 1);
    if (digit == 2 && maxNumber == 3) {
      items[1] = '';
    }
    var result = [];
    result.push(items[1] + items[2] + items[3]);
    result.push(items[1] + items[3] + items[2]);
    result.push(items[2] + items[1] + items[3]);
    result.push(items[2] + items[3] + items[1]);
    result.push(items[3] + items[1] + items[2]);
    result.push(items[3] + items[2] + items[1]);
    result.sort();
    let resultUniq = _.uniq(result);
    let listPoy = _.orderBy(resultUniq, ['text'], ['asc']);
    return listPoy;
  }

  showGroupName(code) {
    const items = this.props.listGroup;
    const group = items.filter((item) => item.code == code);
    return group[0] ? group[0].name : '';
  }

  removeTodo = (todo) => {
    this.props.removeTodo(todo);
  };

  reverseText = (str) => {
    return str.split('').reverse().join('');
  };

  checkReverse = (e) => {
    let value = e.target.checked;
    this.setState({
      reverse: value,
    });
  };

  addNewLotto = (group, value, reverse = false) => {
    let valueText = value.toString();
    const reverseText = this.reverseText(valueText);
    if (group.digit == 3) {
      if (reverse && reverseText !== valueText) {
        let listNumber = this.shuffleNumber(valueText, 3);
        if (listNumber) {
          listNumber.map((num) => {
            if (!this.checkExistLotto(group.code, num)) {
              this.props.addTodo(num, group);
            }
          });
        }
      } else {
        if (!this.checkExistLotto(group.code, valueText)) {
          this.props.addTodo(value, group);
        }
      }
    } else if (group.digit == 2) {
      if (reverse && reverseText !== valueText) {
        if (!this.checkExistLotto(group.code, reverseText)) {
          this.props.addTodo(reverseText, group);
        }
      }
      if (!this.checkExistLotto(group.code, valueText)) {
        this.props.addTodo(valueText, group);
      }
    }

    this.setState({ text: '' });
    this.timeOutPreview = setTimeout(
      () => this.setState({ previewText: '' }),
      3000
    );
    this.sortItems();
  };

  sortItems = () => {
    this.props.setLottoItems(_.uniq(this.props.todos, ['group.code', 'text']));
  };

  checkExistLotto = (groupCode, text) => {
    if (this.props.todos) {
      let find = this.props.todos.filter((item, key) => {
        if (item.group.code == groupCode && item.text == text) {
          return item;
        }
      });
      return find && find.length > 0 ? true : false;
    }
    return false;
  };

  monitorText = (value) => {
    if (value === 'c') {
      this.setState((prev, current) => {
        return {
          ...prev,
          text: prev.text.slice(0, -1),
        };
      });
      this.setState({ previewText: this.state.text });
      return false;
    }

    if (value === 'del') {
      this.setState({
        text: '',
        previewText: '',
      });
      return false;
    }

    this.setState((prev) => {
      return {
        ...prev,
        text: prev.text.toString() + value.toString(),
      };
    });

    let text = this.state.text;
    const count = text.length;
    if (count > 0) {
      this.setState({
        previewText: text,
      });

      const test = this.state.groupSelected[0];
      if (test[0]) {
        if (count > test[0].digit) {
          return {
            ...prev,
            text: prev.text.slice(0, -1),
          };
        }
        return false;
      }
      if (this.state.groupSelected.length > 0 && text) {
        if (count == 2) {
          this.state.groupSelected.map((g, gKey) => {
            const reverse = this.state.reverse2;
            switch (g.code) {
              case 'up2':
                if (count == 2) {
                  this.addNewLotto(g, text, reverse);
                }
                break;
              case 'down2':
                if (count == 2) {
                  this.addNewLotto(g, text, reverse);
                }
            }
          });
        } else if (count == 3) {
          this.state.groupSelected.map((g, gKey) => {
            const reverse = this.state.reverse3;
            switch (g.code) {
              case 'up3':
                if (count == 3) {
                  this.addNewLotto(g, text, reverse);
                }
                break;
              case 'front3':
                if (count == 3) {
                  this.addNewLotto(g, text, reverse);
                }
                break;
              case 'down3':
                if (count == 3) {
                  this.addNewLotto(g, text, reverse);
                }
                break;
              case 'tod3':
                if (count == 3) {
                  this.addNewLotto(g, text, false);
                }
                break;
            }
          });
        }
      }
    }
  };

  /**
   * 
   */
  selectGroup = (item) => {
    const { groupSelected, reverse2, reverse3 } = this.state;
    let items = groupSelected;
    let search = items.filter((v) => v.code == item.code);

    if (item.digit == 3) {
      this.setState({
        reverse2: false,
      });
      items = items.filter((item) => item.digit !== 2);
    } else if (item.digit == 2) {
      this.setState({
        reverse3: false,
      });
      items = items.filter((item) => item.digit !== 3);
    }
    if (search[0]) {
      const index = items.indexOf(item);
      delete items[index];
    } else {
      items.push(item);
      if (item.code == 'tod3') {
        this.setState({
          reverse3: false,
        });
      }
    }
    this.setState({
      groupSelected: items,
    });

    this.setFocusText();
  };

  reset = () => {
    this.setState({
      groupSelected: [],
    });
    this.props.reset();
    this.setState({
      text: '',
      groupSelected: [],
      reverse2: false,
      reverse3: false,
    });
  };
  clear = () => {
    this.props.setLottoItems([]);
    this.props.reset();
    this.setState({
      reverse: false,
      text: '',
      previewText: '',
      group: '',
      // groupSelected: [],
      // reverse2: false,
      // reverse3: false,
      autoPrice: 0,
    });
    toastr.success('', { timeOut: 2 });
  };

  isHasGroupSelected = () => {
    return this.state.groupSelected.length > 0 ? true : false;
  };

  setReverse2 = () => {
    let items = this.state.groupSelected.filter((item) => item.digit !== 3);
    this.setState({
      reverse2: !this.state.reverse2,
      reverse3: false,
      groupSelected: items,
    });
    if (this.isHasGroupSelected()) {
      this.setFocusText();
    }
  };

  setReverse3 = () => {
    let items = this.state.groupSelected;
    items = items.filter((item) => item.code != 'tod3');
    items = items.filter((item) => item.digit !== 2);
    this.setState({
      reverse2: false,
      reverse3: !this.state.reverse3,
      groupSelected: items,
    });
    if (this.isHasGroupSelected()) {
      this.setFocusText();
    }
  };

  setFocusText = () => {
    setTimeout(() => {
      this.numberRef && this.numberRef.current !== undefined
        ? this.numberRef.current.focus()
        : '';
    }, 300);
  };

  setAutoPrice = (price) => {
    if (this.props.todos) {
      let items = [];
      this.props.todos.map((item, key) => {
        item.price = parseFloat(price);
        items.push(item);
      });
      this.props.setLottoItems(items);
    }
  };

  onChangePrice = (e, item) => {
    let value = e.target.value;
    let price = value ? parseFloat(value) : 0;
    let items = this.props.todos;
    console.log(price);
    this.setState({
      autoPrice: value,
    });
    const index = items.indexOf(item);

    items[index].price = parseFloat(price);
    this.props.setLottoItems(items);
    this.setState({
      autoPrice: '',
    });
  };

  setAllPrice = (price) => {
    console.log('setprice', price);
    this.setAutoPrice(price);
    this.setState({
      autoPrice: price,
    });
  };

  sendPoy = () => {
    const { todos } = this.props;
    const count = todos.length;
    let error = false;
    if (count > 0) {
      let find = todos.filter((item) => item.price < 1);
      if (find[0] !== undefined) {
        error = true;
      }
      if (!error) {
        const total = todos.reduce((total, item) => {
          return total + item.price;
        }, 0);

        // let message = ``;
        // let items = this.showItems();
        // items.map((g) => {
        //   message += `#[${this.showGroupName(g.code)}]  `;
        //   if (g.items) {
        //     g.items.map((row) => {
        //       message += `${row.text} =  ${row.price},`;
        //     });
        //   }
        //   message += ` | `;
        // });
        // this.sendLineNotify(message, total);
        Swal.fire({
          title: 'Successfully!',
          text: '',
          icon: 'success',
          confirmButtonText: 'Cool',
        });
        this.clear();
      } else {
        Swal.fire({
          title: 'Error!',
          text: '',
          icon: 'error',
          confirmButtonText: 'Cool',
        });
      }
    } else {
      return false;
    }
  };

  sendLineNotify = (message, price) => {
    // Immediately remove current toasts without using animation
    toastr.remove();
    const accessCode = 'd3MrmVQVeHqpc46FCRKYUXMWmfN1qLK621QBz61dHHq';
    const url = 'https://notify-api.line.me/api/notify';
    const jsonData = {
      message: `   ${price}   ${message}`,
    };
    const requestOption = {
      method: 'POST',
      headers: {
        'content-type': 'application/x-www-form-urlencoded',
        Authorization: `Bearer ` + accessCode,
      },
      data: `message=${message}`,
      url,
    };
    // axios(requestOption)
    //   .then((axiosRes) => {
    //     if (axiosRes.status === 200) {
    //       console.log('Notification Success');
    //       alert(' ^^');
    //     }
    //   })
    //   .catch((error) => {
    //     console.log(error.response.data);
    //   });
  };

  isNumber = (evt) => {
    var charCode = evt.which ? evt.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) return false;
    return true;
  };

  pressNumberPad = (e) => {
    clearTimeout(this.timeOutPreview);
    this.setState({ previewText: '' });
    const value = e.target.value.toString();
    this.monitorText(value);
  };

  undo = () => {
    const { todos } = this.props;
    let list = todos.pop();
    if (list) {
      this.props.setLottoItems(list);
      this.setState({
        items: [],
      });
    }
  };

  amountPoy = () => {
    const { todos } = this.props;
    return todos.reduce((total, item) => {
      return total + item.price;
    }, 0);
  };

  showItems = () => {
    const { todos, setLottoItems } = this.props;
    var result = _.chain(todos)
      .groupBy((x) => x.group.code)
      .map((value, key) => ({
        code: key,
        items: _.orderBy(value, ['text'], ['asc']),
      }))
      .value();
    return result;
  };

  render({ todos, listGroup, text }) {
    const getActiveSelectedButton = (code) => {
      if (code && this.state.groupSelected.length > 0) {
        const item = this.state.groupSelected.filter(
          (item) => item.code == code
        );
        return item[0] !== undefined ? true : false;
      }
      return false;
    };

    return (
      <div id="app" className="p-2 text-center">
        <h1 className="app-title p-2 text-lg font-bold">iNoom LOTTO</h1>

        <div className=" p-1 bg-gray-300 h-50">
          <div className="grid grid-cols-1 sm:grid-cols-2  gap-2 content-start">
            <div>
              <div className="group-container grid gap-1 grid-cols-2 sm:grid-cols-2 content-start">
                {listGroup && listGroup.length > 0
                  ? listGroup.map((item, key) => (
                      <div
                        onClick={() => this.selectGroup(item)}
                        class={`inline-flex rounded-md shadow-sm ${
                          item.classBg
                        }  ${
                          getActiveSelectedButton(item.code) ? ' selected' : ''
                        }`}
                        role="group"
                      >
                        <button type="button" className="font-bold">
                          <span>{item.rate.toString()}</span>
                        </button>

                        <button type="button" className="font-bold">
                          {item.name}
                        </button>
                      </div>
                    ))
                  : ''}
                <div
                  className={`inline-flex rounded-md shadow-sm border border-3 border-red-300 px-6 py-2.5 bg-yellow-600 text-white ${
                    this.state.reverse3 ? 'selected' : ''
                  }`}
                  onClick={() => this.setReverse3()}
                >
                  <button type="button" className="font-bold">
                    3
                  </button>
                </div>
                <div
                  className={`inline-flex rounded-md shadow-sm border border-3 border-red-300 px-6 py-2.5  bg-yellow-600 text-white  ${
                    this.state.reverse2 ? 'selected' : ''
                  }`}
                  onClick={() => this.setReverse2()}
                >
                  <button type="button" className="font-bold">
                    2
                  </button>
                </div>
              </div>

              <div className="my-1">
                <div className="flex">
                  {this.state.groupSelected.length > 0
                    ? this.state.groupSelected.map((g, gIndex) => (
                        <div className="p-1 m-1 text-center border-blue-600  rounded-md bg-yellow-400 ">
                          {g.name}
                        </div>
                      ))
                    : ''}
                  {this.state.reverse2 ? (
                    <div className="p-1 m-1 text-center border-orange-600  rounded-md bg-yellow-400 ">
                      2
                    </div>
                  ) : (
                    ''
                  )}
                  {this.state.reverse3 ? (
                    <div className="p-1 m-1 text-center border-orange-600  rounded-md bg-yellow-400 ">
                      3
                    </div>
                  ) : (
                    ''
                  )}

                  {this.state.previewText ? (
                    <div className="p-1 m-1 text-center  rounded-md bg-green-400 text-black">
                      {this.state.previewText}
                    </div>
                  ) : (
                    ''
                  )}
                </div>
              </div>

              {this.state.groupSelected.length > 0 ? (
                <div className="grid grid-cols-3 gap-1">
                  <button
                    type="button"
                    className="col-span-1 bg-gray-600 text-white font-bolder p-2 cursor-pointer"
                    onClick={this.pressNumberPad}
                    value="1"
                  >
                    1
                  </button>
                  <button
                    type="button"
                    className="col-span-1 bg-gray-600 text-white font-bolder p-2 cursor-pointer"
                    onClick={this.pressNumberPad}
                    value="2"
                  >
                    2
                  </button>
                  <button
                    type="button"
                    className="col-span-1 bg-gray-600 text-white font-bolder p-2 cursor-pointer"
                    onClick={this.pressNumberPad}
                    value="3"
                  >
                    3
                  </button>
                  <button
                    type="button"
                    className="col-span-1 bg-gray-600 text-white font-bolder p-2 cursor-pointer"
                    onClick={this.pressNumberPad}
                    value="4"
                  >
                    4
                  </button>
                  <button
                    type="button"
                    className="col-span-1 bg-gray-600 text-white font-bolder p-2 cursor-pointer"
                    onClick={this.pressNumberPad}
                    value="5"
                  >
                    5
                  </button>
                  <button
                    type="button"
                    className="col-span-1 bg-gray-600 text-white font-bolder p-2 cursor-pointer"
                    onClick={this.pressNumberPad}
                    value="6"
                  >
                    6
                  </button>
                  <button
                    className="col-span-1 bg-gray-600 text-white font-bolder p-2 cursor-pointer"
                    onClick={this.pressNumberPad}
                    value="7"
                  >
                    7
                  </button>
                  <button
                    type="button"
                    className="col-span-1 bg-gray-600 text-white font-bolder p-2 cursor-pointer"
                    onClick={this.pressNumberPad}
                    value="8"
                  >
                    8
                  </button>
                  <button
                    type="button"
                    className="col-span-1 bg-gray-600 text-white font-bolder p-2 cursor-pointer"
                    onClick={this.pressNumberPad}
                    value="9"
                  >
                    9
                  </button>
                  <button
                    type="button"
                    className="col-span-1 bg-orange-600 text-white font-bolder p-2 cursor-pointer"
                    onClick={this.pressNumberPad}
                    value="c"
                  >
                    C
                  </button>
                  <button
                    type="button"
                    className="col-span-1 bg-gray-600 text-white font-bolder p-2 cursor-pointer"
                    onClick={this.pressNumberPad}
                    value="0"
                  >
                    0
                  </button>

                  <button
                    type="button"
                    className="col-span-1 bg-red-600 text-white font-bolder p-2 cursor-pointer"
                    onClick={this.pressNumberPad}
                    value="del"
                  >
                    Del
                  </button>
                </div>
              ) : (
                ''
              )}
            </div>
            {/* ######### RIGHT ########## */}
            <div className="bg-yellow-200 p-1">
              {/* ////////////////////  START INPUT PRICE ALL  /////////////////////////// */}
              {todos && todos.length > 0 ? (
                <div className="my-1 flex">
                  <input
                    type="number"
                    className="bg-green-100 p-1 w-32 border border-gray-300"
                    placeholder=""
                    min={0}
                    ref={this.inputAutoPrice}
                    onInput={(e) => {
                      const price = e.target.value;
                      if (price && parseFloat(price) > 0) {
                        this.setState({
                          autoPrice: price,
                        });
                      }
                    }}
                  />
                  <button
                    type="button"
                    className="bg-gray-600 text-white p-1 border border-gray-800"
                    onClick={() => {
                      if (
                        todos &&
                        todos.length > 0 &&
                        this.state.autoPrice > 0
                      ) {
                        this.setAutoPrice(this.state.autoPrice);
                        this.setState({
                          autoPrice: '',
                        });
                        this.inputAutoPrice.current.value = '';
                      }
                    }}
                  >
                    
                  </button>
                  <button
                    onClick={() => this.setAllPrice(5)}
                    type="button"
                    className="ml-2 bg-gray-600 text-white p-1 border border-gray-800"
                  >
                    5
                  </button>
                  <button
                    onClick={() => this.setAllPrice(10)}
                    type="button"
                    className="bg-gray-600 text-white p-1 border border-gray-800"
                  >
                    10
                  </button>
                  <button
                    onClick={() => this.setAllPrice(15)}
                    type="button"
                    className="bg-gray-600 text-white p-1 border border-gray-800"
                  >
                    15
                  </button>
                  <button
                    onClick={() => this.setAllPrice(20)}
                    type="button"
                    className="bg-gray-600 text-white p-1 border border-gray-800"
                  >
                    20
                  </button>
                  <button
                    onClick={() => this.setAllPrice(50)}
                    type="button"
                    className="bg-gray-600 text-white p-1 border border-gray-800"
                  >
                    50
                  </button>
                  <button
                    onClick={() => this.setAllPrice(100)}
                    type="button"
                    className="bg-gray-600 text-white p-1 border border-gray-800"
                  >
                    100
                  </button>
                </div>
              ) : (
                ''
              )}
              {/* //////////////////// END  INPUT PRICE ALL  /////////////////////////// */}
              {/* ///////////////// BUTTON ACTION ////////////////// */}
              <div className="grid grid-cols-3 gap-1">
                <button
                  type="button"
                  className="rounded block px-1 py-1 w-full  bg-gray-300 text-black"
                  onClick={() => this.undo()}
                >
                  
                </button>
                <button
                  type="button"
                  className="rounded block  px-1 py-1 w-full  bg-gray-400 text-black"
                  onClick={() => this.clear()}
                >
                  
                </button>
                <button
                  type="button"
                  onClick={() => this.sendPoy()}
                  className="bg-green-700 text-white px-1 py-1 w-full block rounded"
                >
                  <i className="fa fa-save"></i>
                  
                </button>
              </div>

              <div class="p-2 text-lg">
                 :{' '}
                <span class="bg-green-700 p-1 text-white border-rounded text-xl">
                  {this.amountPoy()}
                </span>
              </div>

              {/* //////////////////// SHOW RESULT ITEMS /////////////////////////// */}
              <section
                className="bg-gray-200 mt-1"
                style={{
                  maxHeight: 600,
                  overflowX: 'auto',
                }}
              >
                {this.showItems() && this.showItems().length > 0 ? (
                  <section className="">
                    <ul className="lotto-list">
                      {this.showItems().map((group, groupkey) => (
                        <li className="item">
                          <div className="">
                            <div className="text-black-600 lotto-number border border-orange-700 border-solid p-2">
                              <h4 className={`p-2 px-3 font-bold`}>
                                <u> {this.showGroupName(group.code)}</u>
                              </h4>
                              <div class="overflow-x-auto relative">
                                <table class="w-full text-sm text-left text-gray-500 dark:text-gray-400">
                                  <thead class="text-xs text-gray-700 uppercase bg-gray-50 dark:bg-gray-700 dark:text-gray-400">
                                    <tr>
                                      <th scope="col" class="">
                                        
                                      </th>
                                      <th scope="col" class="py-1 px-2"></th>
                                      <th scope="col" class="py-1 px-2">
                                        
                                      </th>
                                      <th scope="col" class="py-1 px-2">
                                        
                                      </th>
                                      <th scope="col" class="py-1 px-2" nowrap>
                                        
                                      </th>
                                      <th
                                        scope="col"
                                        class="py-1 px-2"
                                        nowrap
                                      ></th>
                                    </tr>
                                  </thead>
                                  {group.items.map((row, rowKey) => (
                                    <tbody>
                                      <tr class="border-b dark:bg-gray-800 dark:border-gray-700">
                                        <td class="">{rowKey + 1})</td>
                                        <td class="py-1 px-2">
                                          <button
                                            type="button"
                                            className={`p-1 w-full block font-bold text-md`}
                                          >
                                            {row.text}
                                          </button>
                                        </td>
                                        <td class="py-1 px-2">
                                          <input
                                            onInput={(e) =>
                                              this.onChangePrice(e, row)
                                            }
                                            type="number"
                                            min={0}
                                            style={{
                                              width: 90,
                                              lineHeight: '100%',
                                              height: 30,
                                            }}
                                            value={row.price}
                                            class="
                                          form-control
                                          block
                                          w-full
                                          p-1
                                          text-base
                                          font-normal
                                          text-gray-700
                                          bg-white bg-clip-padding
                                          border border-solid border-gray-300

                                          transition
                                          ease-in-out
                                          m-0
                                          focus:text-gray-700 focus:bg-white focus:border-blue-600 focus:outline-none
                                        "
                                          />
                                        </td>
                                        <td class="py-1 px-2">{row.rate}</td>
                                        <td class="py-1 px-2">
                                          {row.price * row.rate}
                                        </td>
                                        <td class="py-1 px-2">
                                          <button
                                            type="button"
                                            onClick={() => this.removeTodo(row)}
                                            className="bg-red-600 text-white p-1"
                                          >
                                            x
                                          </button>
                                        </td>
                                      </tr>
                                    </tbody>
                                  ))}
                                </table>
                              </div>
                            </div>
                          </div>
                        </li>
                      ))}
                    </ul>
                  </section>
                ) : (
                  ''
                )}
              </section>
            </div>
          </div>

          <br />

          <div className="p-2 text-left  border border-collapse border-spacing-2 border-black-800 bg-gray-300">
            <h3 className="mt-3 underline  decoration-2 font-bolder text-lg">
              
            </h3>
            <p className="">
              - 
              <br />
              - 
              <br />
              - 
              <br />
            </p>
            {/* {JSON.stringify(this.showItems())} */}
          </div>
        </div>
      </div>
    );
  }
}
