import { createStore } from 'redux';

let ACTIONS = {
  ADD_TODO: ({ todos, ...state }, { text, group }) => ({
    todos: [
      ...todos,
      {
        id: Math.random().toString(36).substring(2),
        group,
        text,
        rate: group.rate,
        price: 0,
      },
    ],
    ...state,
  }),

  UNDO_TODO: ({ todos, ...state }, { todo }) => ({
    todos: todos.pop(),
    ...state,
  }),
  REMOVE_TODO: ({ todos, ...state }, { todo }) => ({
    todos: todos.filter((i) => i !== todo),
    ...state,
  }),
  SET_LOTTO_ITEMS: ({ todos, ...state }, { todo }) => ({
    todos: todos,
    ...state,
  }),
  RESET_LOTTO: ({ todos, ...state }, { todo }) => ({
    todos: [],
    ...state,
  }),
};

const INITIAL = {
  todos: [],
  listGroup: [
    {
      code: 'up3',
      name: '3',
      rate: 800,
      digit: 3,
      classBg:
        'inline-block px-6 py-2.5 bg-blue-600 text-white leading-tight uppercase  shadow-md hover:bg-yellow-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out',
    },
    {
      code: 'front3',
      name: '3',
      rate: 400,
      digit: 3,
      classBg:
        'inline-block px-6 py-2.5 bg-blue-600 text-white leading-tight uppercase  shadow-md hover:bg-yellow-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out',
    },
    {
      code: 'down3',
      name: '3',
      rate: 400,
      digit: 3,
      classBg:
        'inline-block px-6 py-2.5 bg-blue-600 text-white leading-tight uppercase  shadow-md hover:bg-yellow-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out',
    },
    {
      code: 'tod3',
      name: '3',
      rate: 120,
      digit: 3,
      classBg:
        'inline-block px-6 py-2.5 bg-blue-600 text-white leading-tight uppercase  shadow-md hover:bg-yellow-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out',
    },
    {
      code: 'up2',
      name: '2',
      rate: 70,
      digit: 2,
      classBg:
        'inline-block px-6 py-2.5 bg-red-600 text-white leading-tight uppercase  shadow-md hover:bg-yellow-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out',
    },
    {
      code: 'down2',
      name: '2',
      rate: 70,
      digit: 2,
      classBg:
        'inline-block px-6 py-2.5 bg-red-600 text-white leading-tight uppercase  shadow-md hover:bg-yellow-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out',
    },
  ],
};

export default createStore(
  (state, action) =>
    action && ACTIONS[action.type]
      ? ACTIONS[action.type](state, action)
      : state,
  INITIAL,
  typeof devToolsExtension === 'function' ? devToolsExtension() : undefined
);
